import logo from './logo.svg';
import './App.css';
import Header from './components/Header';
import Footer from './components/Footer';
import React, { Suspense } from 'react';
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
import Loading from './components/Loading';
function App() {

  const NotFound = React.lazy(() => import('./components/NotFound'))
  const ProductManager = React.lazy(() => import('./features/ProductManager'))
  const CategoryManager = React.lazy(() => import('./features/CategoryManager'))
  return (
    <div className="App">
      <Suspense fallback={<Loading />}>
        <BrowserRouter>
          <Header />
          <div className="main">
            <Switch>
              <Redirect exact from="/" to="/product" />
              <Route path="/product" component={ProductManager} />
              <Route path="/category" component={CategoryManager} />
              <Route component={NotFound} />
            </Switch>
          </div>

          <Footer />
        </BrowserRouter>

      </Suspense>



    </div>
  );
}

export default App;
